
#app {
  font-family: 'PingFangSC-Regular', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #fff;
  /* background:rgb(12,12,12) */
}
input{
  outline:none;
}
p{
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}
body{
  margin: 0;
  padding: 0rem;
  background: #000;
}
.input-con{
  padding:0 .16rem;
  position: relative;
  overflow: hidden;
}
.input{
  display: block;
  width: 100%;
  position: relative;
  margin-top: .32rem;
  height: .2rem;
  line-height: .2rem;
  font-size: .14rem;
  background:rgba(0,0,0,0);
  text-decoration:none;
  color: #fff;
  outline:none;
  border-style:none;
  /* border: 1px solid #fff; */
}
.white-line{
  position: relative;
  margin-top:.09rem;
  height: 1px;
  /* transform: scaleY(0.5);
  -webkit-transform: scaleY(0.5); */
  background: rgb(53,53,53);
}
.upload-con{
  margin-top: .32rem;
  position: relative;
}
.before-upload{
  width: 100%;
  position: relative;
}
.before-upload .add-con{
  position: relative;
  display: block;
  margin: 0 .16rem;
}
.before-upload .add-icon{
  display: block;
  width: .26rem;
  height: .26rem;
  margin-left: -.13rem;
  left: 50%;
  top: .5rem;
  position: absolute;
}
.before-upload .add-tip{
  font-size: .12rem;
  line-height: .17rem;
  color: #fff;
  position: absolute;
  top: .83rem;
  text-align: center;
  width: 100%;
}
.upload-con .input-file{
    display: block;
    width: 100%;
    height: 1.5rem;
    top: 0;
    left: 0;
    position: absolute;
    opacity: 0;
}
.upload-con .upload-img{
  display: block;
  height: 1.5rem;
  width: 2rem;
  margin: 0 auto;
  -o-object-fit: cover;
     object-fit: cover;
}
.upload-example{
  margin-top: .16rem;
  position: relative;
  padding: 0 .16rem;
  height: 1.42rem;
  margin-bottom: .32rem;
}
.upload-example .title{
  font-size: .14rem;
  color: rgb(133,133,133);
  text-align: left;
  line-height: .2rem;
  margin-bottom: .12rem;
}
.upload-example .example-img{
  width: 1.48rem;
  height: 1.11rem;
  display: block;
  position: absolute;
  -o-object-fit: cover;
     object-fit: cover;
  top: .32rem;
  left: .16rem;
}
.upload-example .example-desc{
  margin-left: 1.64rem;
  font-size: .12rem;
  color: rgb(133,133,133);
  text-align: left;
  margin-bottom: .12rem;
}
.submit-btn{
  margin: .16rem;
  height: .42rem;
  background: rgb(255,227,47);
  color: rgb(0,0,0);
  font-size: 14px;
  line-height: .42rem;
  text-align: center;
  font-weight: 600;
  border-radius: .21rem;
}
.submit-btn-disabled{
  margin: .16rem;
  height: .42rem;
  background: #554d16;
  color: rgb(0,0,0);
  font-size: 14px;
  line-height: .42rem;
  text-align: center;
  font-weight: 600;
  border-radius: .21rem;
}
.tip{
  padding-left: .35rem;
  font-size: .14rem;
  color: #fff;
  text-align: left;
  padding-bottom: .5rem;
  position: relative;
}
.tip .img-outer{
  position: absolute;
  width: .13rem;
  height: .13rem;
  left: .16rem;
  top: .03rem;
}
.tip .img-inner{
  position: absolute;
  width: .07rem;
  height: .07rem;
  left: .19rem;
  top: .06rem;
}
.tip .yellow{
  color: rgb(255,227,47);
}
.submited-text{
  font-size: .14rem;
  color: #fff;
  line-height: .2rem;
  margin-top: 2.1rem;
}
.submit-btn-leave{
  margin: .16rem;
  height: .42rem;
  background: #fff;
  color: rgb(0,0,0);
  font-size: 14px;
  line-height: .42rem;
  text-align: center;
  font-weight: 600;
  border-radius: .21rem;
  margin-top: .6rem;
}
